import {request} from '../../request.js'
export default {
	data() {
		return {
			// 店铺类型(线上/线下)
			typeList: [
				{
					name: '线上',
					value: 0
				},
				{
					name: '线下',
					value: 1
				}
			],
			// 类型
			offline: 0,
			index:0,
			checked: false,
			// 控制当前处于第几步
			isStatus: 1,
			// 个人
			personChecked: true,
			// 企业
			Companychecked: false,
			currentIndex: 0,
			// 公司类型选择的
			companyList: [
				{
					name: '私营企业',
					value: 0
				},
				{
					name: '个体户',
					value: 1
				},
				{
					name: '外企',
					value: 2
				},
				{
					name: '中外合资',
					value: 3
				}		
			],
			companyIndex: 0,
			// 店铺认证同意radio
			companyChecked: false,
			// 省市区数据
			province: [{'province_name':'请选择'}],
			city:  [{'city_name':'请选择'}],
			district: [{'distrcit_name':'请选择'}],
			provinceId:[],
			cityId:[],
			districtId:[],
			selectProvince:1,
			selectCity:0,
			selectDistrict:0,
			
			// 储存图片前缀
			$api: '',
			// 储存用户的token
			token: '',
			// 储存身份证正面图片
			frontImg: '',
			// 储存身份证反面图片
			backImg: '',
			// 储存手持身份证图片
			shareImg: '',
			//营业执照电子版
			businessImg:'',
			//组织机构代码电子版
			organizationImg:'',
			//一般纳税人证明
			proveImg:'',
			//公司名称
			corporate:'',
			//详细地址
			address:'',
			//姓名
			full_name:'',
			// 用户手机
			user_phone:'',
			// 电子邮箱
			email:'',
			//----个人----
			//身份证号
			idcard:'',
			//银行卡开户名
			bank_username:'',
			//个人银行卡号
			bank_card:'',
			//开户银行名称
			bank_name:'',
			// 支行联行号
			interbank:'',
			//----企业----
			//公司类型
			company_id:0,
			//公司联系电话
			company_phone:'',
			// 公司人数
			company_number:'',
			// 注册资金
			capital:'',
			// 营业执照
			license:'',
			// 法定营业范围
			business_scope:'',
			// 组织机构代码
			organization_code:'',
			//店铺名称
			shop_name:'',
			//店铺等级
			shop_grade_name:'',
			//店铺类型
			
			statusHeight: '',
			// 选择银行相关
			bank: [],
			bankIndex: 0,
			bankSelected: '',
			// 储存用户是否有申请过入驻状态
			applyStatus: '',
			// 订单号
			orderCode: '',
			orderMsg: '',
			// 店铺类型
			shopTypeList:[],
			shopIndex: 0,
			// 店铺id
			shop_group_id: '',
			// 店铺名
			shop_group_name: '',
			// 店铺等级
			shopGrade: [],
			gradeIndex: 0,
			shop_type_id: '',
			shop_type_name:'',
			$src: this.$src
		};
	},
	onLoad() {
		
		// 读取当前设备的可视高度
		uni.getSystemInfo({
			success: res => {
				this.statusHeight = res.windowHeight - 150 + 'px';
			}
		})
		// 赋值图片前缀
		this.$api = this.$api;
		this.getProvince();
		// 获取当前用户的token
		uni.getStorage({
			key:'token',
			success: res => {
				this.token = res.data[0]
			}
		});
		// 请求银行名称
		this.getBankName()
		// 检测当前账户是否有申请过商家入驻
		this.checkApply();
		// 读取店铺类型
		this.getShopType();
		// 读取店铺等级
		this.getShopGrade();
	},
	methods:{
		// 同意协议
		agreeDeal() {
			this.checked = !this.checked
		},
		// 商家协议下一步按钮
		stepOne() {
			if(!this.checked) {
				uni.showToast({
					title: '请先同意相关协议',
					icon: 'none'
				})
				return
			}
			this.isStatus = 2
			
		},
		// 选择线上或线下
		chooseType(event) {
			this.index = event.detail.value;
			this.offline = this.typeList[event.detail.value].value;
			console.log(this.offline)
		},
		// 选择个人或企业
		chooseId(event) {
			this.currentIndex = event.detail.value;
			this.personChecked = !this.personChecked;
			this.Companychecked = !this.Companychecked;
		},
		// 返回上一步
		goBack() {
			this.isStatus = 2;
		},
		// 提交审核
		stepThree() {
			if(!this.shop_name) {
				uni.showToast({
					title: '请填写店铺名称',
					icon: 'none'
				})
				return
			}
			// 提交参数
			var company_name=this.corporate;
			var company_address_detail=this.address;
			var contacts_name=this.full_name;
			var contacts_phone=this.user_phone;
			var contacts_email=this.email;				
			var company_province_id=this.selectProvince;
			var company_city_id=this.selectCity;
			var company_district_id=this.selectDistrict;
			
			var shop_name=this.shop_name;
			var assistant_code = 0;
			var recommend_uid = 0;
			var shop_type_id= this.shop_type_id ? this.shop_type_id : this.shopGrade[this.gradeIndex].type_id;
			var shop_group_id=this.shop_grade_id? this.shop_group_id: this.shopTypeList[this.shopIndex].group_sort;
			var shop_type_name=this.shop_type_name ? this.shop_type_name : this.shopGrade[this.gradeIndex].type_name;
			var shop_group_name=this.shop_group_name? this.shop_group_name: this.shopTypeList[this.shopIndex].group_name; 
			
			if(this.personChecked){
					var apply_type = 1;
					var contacts_card_no=this.idcard;
					var contacts_card_electronic_1=this.frontImg;
					var contacts_card_electronic_2=this.backImg;
					var contacts_card_electronic_3=this.shareImg;
					var bank_account_name=this.bank_username;
					var bank_account_number=this.bank_card;
					var bank_name=this.bank_name ? this.bank_name : this.bank[0].name;
					var bank_code=this.interbank ? this.interbank: '';
				}else{
					var apply_type = 2;
					var company_type=this.company_id
					var company_phone=this.company_phone;
					var company_employee_count=this.company_number
					var company_registered_capital=this.capital
					var business_licence_number=this.license
					var business_sphere=this.business_scope
					var business_licence_number_electronic=this.businessImg
					var organization_code=this.organization_code ? this.organization_code: ''
					var organization_code_electronic=this.organizationImg ? this.organizationImg : ''
					var general_taxpayer=this.proveImg ? this.proveImg : ''
				}
				
				request({
					url: "index.php?s=wap/shop/applySecondCompanyInfo",
					method: "POST",
					data: {
						"offline": this.offline, 
						"apply_type":apply_type,
						"company_name":company_name,
						"company_province_id":company_province_id,
						"company_city_id":company_city_id,
						"company_district_id":company_district_id,
						"company_address_detail":company_address_detail,
						"contacts_name":contacts_name,
						"contacts_phone":contacts_phone,
						"contacts_email":contacts_email ? contacts_email: '',
						"contacts_card_no":contacts_card_no,
						"contacts_card_electronic_1":contacts_card_electronic_1,
						"contacts_card_electronic_2":contacts_card_electronic_2,
						"contacts_card_electronic_3":contacts_card_electronic_3,
						"bank_account_name":bank_account_name,
						"bank_account_number":bank_account_number,
						"bank_name":bank_name ? bank_name : '招商银行',
						"bank_code":bank_code,
						"shop_name":shop_name,
						"shop_type_name":shop_type_name,
						"shop_group_name":shop_group_name,
						"shop_type_id":shop_type_id,
						"shop_group_id":shop_group_id,
						"company_type":company_type,
						"company_phone":company_phone,
						"company_employee_count":company_employee_count,
						"company_registered_capital":company_registered_capital,
						"business_licence_number":business_licence_number,
						"business_sphere": business_sphere,
						"business_licence_number_electronic":business_licence_number_electronic,
						"organization_code": organization_code,
						"organization_code_electronic": organization_code_electronic,
						"general_taxpayer": general_taxpayer,
						"recommend_uid": recommend_uid,
					}
				}).then(res => {
					if(res.data.apply_id > 0 ) {
						uni.showToast({
							title: '提交审核成功',
						});
						this.orderCode = res.data.apply_no;
					}
					this.isStatus = 4
				})	
		},
		// 检测用户是否有提交过申请
		checkApply() {
			request({
				url: 'index.php?s=wap/shop/applyState'
			}).then(res => {
				this.orderMsg = res;
				this.applyStatus = res.data.state.apply_state;
				// 状态为2表示审核通过
				if(this.applyStatus == 2) {
					uni.showModal({
						title: '',
						content: '审核已通过,无需再提交',
						showCancel: false,
						success: res => {
							if(res.confirm) {
								uni.switchTab({
									url: '/pages/mine/mine'
								})
							}
						},
					});
				}
				// 状态为1表示前台提交成功但还没审核通过
				if(this.applyStatus == 1 && res.data.pay_ment == 0) {
					uni.showModal({
						title: '',
						content: '已提交过申请,是否前往付款',
						showCancel: true,
						success: res => {
							if(!res.confirm) {
								uni.switchTab({
									url: '/pages/mine/mine'
								})
							}
							else {
								this.isStatus = 4;
								this.orderCode = this.orderMsg.data.state.apply_no;
							}
						},
					});
				}
				// 状态为1且已付款
				if(this.applyStatus == 1 && res.data.pay_ment == 1) {
					uni.showModal({
						title: '',
						content: '已提交过申请,请耐心等待审核!',
						showCancel: false,
						success: res => {
							if(res.confirm) {
								uni.switchTab({
									url: '/pages/mine/mine'
								})
							}
						},
					});
				}
			})
		},
		// 付款
		pay() {
			uni.setStorage({
				key: 'price',
				data: 600
			})
			uni.getStorage({
				key: 'token',
				fail: res => {
				},
				success:res =>{
					let token = res.data[0];
					let redirectUrl = encodeURIComponent("https://admin.dxsc.vip/index.php?s=/wap/pay/ApiwchatPay&no="+this.orderCode+"&token="+token+"&type=1");
					let url = 'https://open.weixin.qq.com/connect/oauth2/authorize?appid=wx7cffa961be69b86e&redirect_uri='+redirectUrl+'&response_type=code&scope=snsapi_userinfo&state=STATE#wechat_redirect';
					window.location.href = url;
				}
			});
		},
		// 店铺认证
		companyAgree() {
			this.companyChecked = !this.companyChecked
		},
		// 店铺认证下一步按钮
		stepTwo() {
			if(!this.companyChecked) {
				uni.showToast({
					title: '请先同意相关协议',
					icon: 'none'
				})
				return
			}
			if(!this.corporate) {
				uni.showToast({
					title: '请先填写公司名称',
					icon: 'none'
				})
				return
			}
			if(!this.address) {
				uni.showToast({
					title: '请先填写公司地址',
					icon: 'none'
				})
				return
			}
			if(!this.full_name) {
				uni.showToast({
					title: '请先填写姓名',
					icon: 'none'
				})
				return
			}
			if(!this.user_phone) {
				uni.showToast({
					title: '请先填写联系方式',
					icon: 'none'
				})
				return
			}
			
			var myreg = /^[1][3,4,5,7,8,9][0-9]{9}$/;
			if (!myreg.test(this.user_phone)) {
			    uni.showToast({
					title: '请先填写正确的手机号码',
					icon: 'none'
				})
			    return
			}
			
			//---个人---
			if (this.personChecked) {
				if(!this.idcard) {
					uni.showToast({
						title: '请先填写身份证',
						icon: 'none'
					})
					return
				}
				if(!this.bank_username) {
					uni.showToast({
						title: '请先填写银行卡开户名',
						icon: 'none'
					})
					return
				}
				if(!this.bank_card) {
					uni.showToast({
						title: '请先填写银行卡号',
						icon: 'none'
					})
					return
				}
				if(!this.frontImg) {
					uni.showToast({
						title: '请上传身份证正面',
						icon: 'none'
					})
					return
				}
				if(!this.backImg) {
					uni.showToast({
						title: '请上传身份证返面',
						icon: 'none'
					})
					return
				}
			}
			
			//企业
			if (this.Companychecked) {
				if(!this.company_phone) {
					uni.showToast({
						title: '请填写公司联系电话',
						icon: 'none'
					})
					return
				}
				if(!this.company_number) {
					uni.showToast({
						title: '请填写员工人数',
						icon: 'none'
					})
					return
				}
				if(!this.capital) {
					uni.showToast({
						title: '请填写注册资金',
						icon: 'none'
					})
					return
				}
				if(!this.license) {
					uni.showToast({
						title: '请填写营业执照',
						icon: 'none'
					})
					return
				}
				if(!this.business_scope) {
					uni.showToast({
						title: '请填写法定营业范围',
						icon: 'none'
					})
					return
				}
				if(!this.businessImg) {
					uni.showToast({
						title: '请上传营业执照号电子版',
						icon: 'none'
					})
					return
				}
				
				
			}
			
			
			this.isStatus = 3
			
		},
		// 上传身份证正面
		chooseFrontImg() {
			this.upLoadImg('frontImg')
		},
		// 上传身份证反面
		chooseBackImg() {
			this.upLoadImg('backImg')
		},
		// 手持身份证照片
		chooseShareImg() {
			this.upLoadImg('shareImg')
		},
		// 营业执照电子版
		chooseBusinessImg() {
			this.upLoadImg('businessImg')
		},
		// 组织机构代码证电子版
		chooseOrganizationImg() {
			this.upLoadImg('organizationImg')
		},
		// 一般纳税人证明
		chooseProveImg() {
			this.upLoadImg('proveImg')
		},
		
		// 上传图片
		upLoadImg(type) {
			uni.chooseImage({
				count: 1,
				success: res => {
					let path = res.tempFilePaths[0];
					// 上传图片
					uni.uploadFile({
						url: this.$api +'index.php?s=/wap/shop/upload_img?token='+this.token,
						filePath: path,
						name: 'cardIdFront',
						success: res => {
							let data = JSON.parse(res.data);
							if(data.code == 1) {
								// 储存图片路径
								switch (type) {
									case 'frontImg':
									this.frontImg = data.patch;
									break;
									case 'backImg':
									this.backImg = data.patch;
									break;
									case 'shareImg':
									this.shareImg = data.patch;
									break
									case 'businessImg':
									this.businessImg = data.patch;
									break
									case 'organizationImg':
									this.organizationImg = data.patch;
									break
									case 'proveImg':
									this.proveImg = data.patch;
									break
								}
								uni.showToast({
									title: '上传成功',
									icon: 'none'
								})
							}
						}
					})
				}
			})
		},
		// 省市区
		bindSelectProvince(e){
			console.log(this.province);
			this.selectProvince = e.detail.value;
			this.getCity();
		},
		bindSelectCity(e){
			this.selectCity = e.detail.value;
			this.getDistrict();
		},
		bindSelectDistrict(e){
			this.selectDistrict = e.detail.value;
		},
		getProvince(){
			let that = this;
			let address_id = this.address_id;
			let address_info = this.address_info;
			request({
				url:'index.php?s=/wap/index/getProvince',
				method:'POST',
			}).then(res=>{
				let province = res.data;
				if(address_id>0){
					for(var i in province){
						if(province[i].province_id==address_info.province){
							that.selectProvince = i;
							console.log(that.selectProvince);
						}
					}
				}
				that.province = province;
				that.getCity();
			});
		},
		getCity(){
			let that = this;
			let address_id = this.address_id;
			let address_info = this.address_info;
			request({
				url:'index.php?s=/wap/index/getCity',
				data:{
					province_id:that.province[that.selectProvince].province_id
				},
				method:'POST',
			}).then(res=>{
				let city = res.data;
				if(address_id>0){
					for(var i in city){
						if(city[i].city_id==address_info.city){
							that.selectCity = i;
							console.log(that.selectCity);
						}
					}
				}
				that.city = city;
				that.getDistrict();
			});
		},
		getDistrict(){
			let that = this;
			let address_id = this.address_id;
			let address_info = this.address_info;
			request({
				url:'index.php?s=/wap/index/getDistrict',
				data:{
					city_id:that.city[that.selectCity].city_id
				},
				method:'POST',
			}).then(res=>{
				let district = res.data;
				if(address_id>0){
					for(var i in district){
						if(district[i].district_id==address_info.district){
							that.selectDistrict = i;
							console.log(that.selectDistrict);
						}
					}
				}
				that.district = district;
			});
		},
		chooseCompanyType(e){
			this.company_id = e.detail.value;
		},
		// 选择银行
		chooseBankIndex(event) {
			this.bankIndex = event.detail.value;
			this.bank_name = this.bank[event.detail.value].name;
			console.log(this.bank_name);
		},
		// 请求银行类别
		getBankName() {
			request({
				url: 'index.php?s=/wap/index/getBank',
			}).then(res => {
				// console.log(res.data)
				for(let key in res.data) {
					this.bank.push({value: key,name: res.data[key]});
				}
			});
		},
		// 请求商店类型
		getShopType() {
			request({
				url: 'index.php?s=/wap/shop/getShopGroup'
			}).then(res => {
				for(let i = 0;i < res.data.length;i++) {
					this.shopTypeList.push({group_name: res.data[i].group_name,group_sort: res.data[i].group_sort})
				};
				console.log(this.shopTypeList);
			})
		},
		// 改变商店类型
		chooseShopType(event) {
			this.shopIndex = event.detail.value;
			// 门店id
			this.shop_group_id = this.shopTypeList[this.shopIndex].group_sort;
			// 门店类型
			this.shop_group_name = this.shopTypeList[this.shopIndex].group_name;
		},
		// 请求店铺等级
		getShopGrade() {
			request({
				url: 'index.php?s=/wap/shop/getShopIns'
			}).then(res => {
				console.log(res);
				for(let i = 0;i < res.data.length;i++) {
					this.shopGrade.push({type_name: res.data[i].type_name,type_id: res.data[i].instance_typeid})
				};
				console.log(this.shopGrade)
			})
		},
		// 改变店铺等级
		chooseShopGrade(event) {
			this.gradeIndex = event.detail.value;
			// 门店id
			this.shop_type_id = this.shopGrade[this.gradeIndex].type_id;
			// 门店类型
			this.shop_type_name = this.shopGrade[this.gradeIndex].type_name;
		}
		
		
		
		
	}
}
